import React from 'react';
import { Field } from 'redux-form';

import { Box, Flex } from '@rebass/grid';

import { CheckboxField, Delimiter, NumberFormatField, SelectField } from 'components';
import { loanRepaymentCalcTypesOptions } from 'consts';
import { formErrorUtil, stringsUtil } from 'utils';

interface ILoanDetails {
  isEditMode?: boolean;
  isReadOnly?: boolean;
  maxInstalmentsValue: string;
  minInstalmentsValue: string;
  defaultInstalmentsValue: string;
}

const LoanDetails: React.FC<ILoanDetails> = ({
  isEditMode,
  isReadOnly,
  maxInstalmentsValue,
  minInstalmentsValue,
  defaultInstalmentsValue,
}) => {
  const instalmentsNumberValidation = React.useMemo(
    () => {
      if (stringsUtil.toNumber(defaultInstalmentsValue) > 0) {
        return formErrorUtil.lessFieldValue(
          'defaultInstalments',
          'Must be < "Default Instalments"'
        );
      } else {
        return formErrorUtil.isInteger;
      }
    },
    [defaultInstalmentsValue]
  );

  const minInstalmentsValidation = React.useMemo(
    () => {
      if (stringsUtil.toNumber(maxInstalmentsValue) > 0) {
        return formErrorUtil.lessOrEqualFieldValue(
          'maxInstalments',
          'Must be <= "Max instalments"'
        );
      } else {
        return formErrorUtil.isInteger;
      }
    },
    [maxInstalmentsValue]
  );

  const defaultInstalmentsValidation = React.useMemo(
    () => {
      const max = stringsUtil.toNumber(maxInstalmentsValue);
      const min = stringsUtil.toNumber(minInstalmentsValue);
      const validation = [];

      if (max > 0) {
        validation.push(formErrorUtil.lessOrEqualExactValue(max, `Max Instalments: ${max}`));
      }

      if (min > 0) {
        validation.push(formErrorUtil.greaterOrEqualExactValue(min, `Min Instalments: ${min}`));
      }

      return validation;
    },
    [maxInstalmentsValue, minInstalmentsValue]
  );

  return (
    <Box mx="-8px">
      <Flex
        alignItems="flex-end"
        flexWrap="wrap"
      >
        <Box width="300px" p="8px">
          <Field
            id="repaymentCalculationType"
            name="repaymentCalculationType"
            component={SelectField}
            label="Repayment Calculation Type"
            placeholder="Select Method"
            options={loanRepaymentCalcTypesOptions}
            isDisabled={isReadOnly}
            validate={[formErrorUtil.isRequired]}
          />
        </Box>
        <Box width="150px" p="8px">
          <Field
            id="minLoan"
            name="minLoan"
            component={NumberFormatField}
            placeholder="0.00"
            fixedDecimalScale={true}
            decimalScale={2}
            label="Min Loan"
            disabled={isReadOnly || isEditMode}
            validate={[formErrorUtil.isRequired]}
          />
        </Box>
        <Box width="150px" p="8px">
          <Field
            id="maxLoan"
            name="maxLoan"
            component={NumberFormatField}
            placeholder="0.00"
            fixedDecimalScale={true}
            decimalScale={2}
            label="Max Loan"
            disabled={isReadOnly || isEditMode}
            validate={[formErrorUtil.isRequired]}
          />
        </Box>
        <Delimiter />
        <Box width="150px" p="8px">
          <Field
            id="minInstalments"
            name="minInstalments"
            component={NumberFormatField}
            placeholder="0"
            label="Min Instalments"
            disabled={isReadOnly || isEditMode}
            validate={[
              minInstalmentsValidation,
            ]}
          />
        </Box>
        <Box width="150px" p="8px">
          <Field
            id="maxInstalments"
            name="maxInstalments"
            component={NumberFormatField}
            placeholder="0"
            label="Max Instalments"
            disabled={isReadOnly || isEditMode}
            validate={[
              formErrorUtil.isInteger,
            ]}
          />
        </Box>
        <Box width="150px" p="8px">
          <Field
            id="defaultInstalments"
            name="defaultInstalments"
            component={NumberFormatField}
            placeholder="0"
            label="Default Instalments"
            disabled={isEditMode || isReadOnly}
            autoFocus={true}
            validate={[
              formErrorUtil.isRequired,
              formErrorUtil.isInteger,
              ...defaultInstalmentsValidation,
            ]}
          />
        </Box>
        <Box width="150px" p="8px">
          <Field
            id="promotionalCycles"
            name="promotionalCycles"
            component={NumberFormatField}
            placeholder="0"
            label="Promotional Cycles"
            disabled={isReadOnly || isEditMode}
            validate={[
              instalmentsNumberValidation,
            ]}
          />
        </Box>
        <Delimiter />
        <Box width="150px" p="8px">
          <Field
            id="restrictStartCycle"
            name="restrictStartCycle"
            component={NumberFormatField}
            placeholder="0"
            label="Restrict Start Cycle"
            disabled={isReadOnly || isEditMode}
            validate={[
              formErrorUtil.isInteger,
            ]}
          />
        </Box>
        <Box width="150px" p="8px">
          <Field
            id="restrictEndCycle"
            name="restrictEndCycle"
            component={NumberFormatField}
            placeholder="0"
            label="Restrict End Cycle"
            disabled={isReadOnly || isEditMode}
            validate={[
              formErrorUtil.isInteger,
            ]}
          />
        </Box>
        <Box width="150px" p="8px">
          <Field
            id="postingDelay"
            name="postingDelay"
            component={NumberFormatField}
            placeholder="0"
            label="Posting Delay"
            disabled={isReadOnly}
            validate={[
              formErrorUtil.isRequired,
              formErrorUtil.isInteger,
            ]}
          />
        </Box>
        <Box width={[1]} p="8px">
          <Field
            id="allowOverpayment"
            name="allowOverpayment"
            component={CheckboxField}
            label="Allow overpayment"
            disabled={isReadOnly}
          />
        </Box>
        <Box width={[1]} p="8px">
          <Field
            id="allowHolidays"
            name="allowHolidays"
            component={CheckboxField}
            label="Allow Holidays"
            disabled={isReadOnly}
          />
        </Box>
      </Flex>
    </Box>
  );
};

export default LoanDetails;
