import Select, {
  components,
  IndicatorProps,
  OptionProps,
  Props as SelectProps,
} from 'react-select';
import { MultiValueRemoveProps } from 'react-select/src/components/MultiValue';

import styled from 'theme';
import { scrollbarCss } from 'theme/styles';

import { ChevronDownIcon, CloseIcon } from './../../../Icons';
import MultiSelectOption from './Option';

import { customStyles } from './customStyles';

const WithCustomScrollbar = styled.div`
  width: 100%;
  .form-select__menu-list {
    ${scrollbarCss};
  }
`;

const DropdownIndicator = <T extends {}>(props: IndicatorProps<T, boolean>) => {
  return components.DropdownIndicator && (
    <components.DropdownIndicator {...props}>
      <ChevronDownIcon size="16" />
    </components.DropdownIndicator>
  );
};

const MultiValueRemove = <T extends {}>(props: MultiValueRemoveProps<T>) => {
  return components.MultiValueRemove && (
    <components.MultiValueRemove {...props}>
      <CloseIcon />
    </components.MultiValueRemove>
  );
};

interface IInputValue {
  inputValue: string;
}

const loadingMessage = (obj: IInputValue) => 'Options are being fetched';
const defaultNoOptionsMsg = (obj: IInputValue) => 'No options';
const preventNoOptionsMsg = (): null => null;

interface ISelectInput<T = object> extends SelectProps<T> {
  modifySelectOption?: (data: OptionProps<T, boolean>) => void;
  isCustomSingleValue?: boolean;
  onChange?: any;
}

export const SelectInput = (props: ISelectInput) => {
  const { id, isMulti, isCustomSingleValue, onChange, ...extraProps } = props;

  return (
    <WithCustomScrollbar>
      <Select
        inputId={id}
        classNamePrefix="form-select"
        styles={customStyles}
        loadingMessage={loadingMessage}
        className="form-select"
        isMulti={isMulti}
        isClearable={true}
        openMenuOnFocus={true}
        tabSelectsValue={false}
        isSearchable={true}
        closeMenuOnSelect={!isMulti}
        blurInputOnSelect={!isMulti}
        hideSelectedOptions={!isMulti}
        openMenuOnClick={!isCustomSingleValue}
        noOptionsMessage={isCustomSingleValue ? preventNoOptionsMsg : defaultNoOptionsMsg}
        onChange={onChange}
        components={
          {
            Option: isMulti ? MultiSelectOption : components.Option,
            DropdownIndicator: isCustomSingleValue ? null : DropdownIndicator,
            MultiValueRemove,
            IndicatorSeparator: null,
          }
        }
        maxMenuHeight={165}
        {...extraProps}
      />
    </WithCustomScrollbar>
  );
};

export default SelectInput;
