export const prepareLoanToRender = (data: ILoanProductData) => {
  if (!data) {
    return null;
  }

  const {
    allow_holidays,
    allow_overpayment,
    default_instalments,
    repayment_calculation_type,
    max_instalments,
    max_loan,
    min_instalments,
    min_loan,
    posting_delay,
    product_id,
    promotional_cycles,
    restrict_end_cycle,
    restrict_start_cycle,
  } = data;

  return {
    allowHolidays: allow_holidays === yesNoConst.YES,
    allowOverpayment: allow_overpayment === yesNoConst.YES,
    defaultInstalments: default_instalments,
    repaymentCalculationType: loanRepaymentCalcTypesOptions
      .find(el => el.value === repayment_calculation_type),
    maxInstalments: max_instalments,
    maxLoan: max_loan,
    minInstalments: min_instalments,
    minLoan: min_loan,
    postingDelay: posting_delay,
    productId: product_id,
    promotionalCycles: promotional_cycles,
    restrictEndCycle: restrict_end_cycle,
    restrictStartCycle: restrict_start_cycle,
  };
};

