import { connect } from 'react-redux';
import { bindActionCreators, Dispatch } from 'redux';
import { formValueSelector, isValid } from 'redux-form';

import ProductRulesForm from './ProductRulesForm';

import { formNamesConst } from 'consts';

import {
  actionTypesSelector,
  currentProductRuleSelector,
  currentProductTypeSelector,
  dictionaryEventsSelector,
  eventDataElemsOptionsForRulesSelector,
  handleFilterDictionaryEventDataElemsById,
  handleGetDictionaryEvents,
  handleGetDictionaryTransactionTypes,
  handleGetProductAprsFeesRewards,
  handleGetProductRule,
  handleUpdateProductRule,
  isEventsLoadingSelector,
  isProductRuleLoadingSelector,
  IStoreState,
  productAprsOptionsForRulesSelector,
  productFeesOptionsForRulesSelector,
  productRewardsOptionsForRulesSelector,
  resetProductRule,
  transTypesOptionsForRulesSelector,
} from 'store';

const formSelector = formValueSelector(formNamesConst.PRODUCT_RULES);

const mapStateToProps = (state: IStoreState) => ({
  actionTypes: actionTypesSelector(state),
  actionTypeSelectedValue: formSelector(state, 'actionType'),
  aprsOptions: productAprsOptionsForRulesSelector(state),
  eventDataElemsOptions: eventDataElemsOptionsForRulesSelector(state),
  eventIdSelectedValue: formSelector(state, 'eventId'),
  events: dictionaryEventsSelector(state),
  feesOptions: productFeesOptionsForRulesSelector(state),
  initialValues: currentProductRuleSelector(state),
  isEventsLoading: isEventsLoadingSelector(state),
  isRuleLoading: isProductRuleLoadingSelector(state),
  productType: currentProductTypeSelector(state),
  rewardsOptions: productRewardsOptionsForRulesSelector(state),
  transactionTypesOptions: transTypesOptionsForRulesSelector(state),
  valid: isValid(formNamesConst.PRODUCT_RULES)(state),
});

const mapDispatchToProps = (dispatch: Dispatch) => bindActionCreators(
  {
    getEvents: handleGetDictionaryEvents,
    getRule: handleGetProductRule,
    resetRule: resetProductRule,
    updateRule: handleUpdateProductRule,
    getProductAprsFeesRewards: handleGetProductAprsFeesRewards,
    getTransactionTypes: handleGetDictionaryTransactionTypes,
    filterDictionaryEventDataElemsById: handleFilterDictionaryEventDataElemsById,
  },
  dispatch
);

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(ProductRulesForm);
