import React from 'react';
import { InjectedFormProps, reduxForm } from 'redux-form';
import { ImmutableArray } from 'seamless-immutable';

import { OkCancelButtons } from 'components';

import { formNamesConst, IActionTypeConfig } from 'consts';
import { ProductRules } from 'containers/ProductDesigner/Products/components';
import {
  IDictionaryEvent,
  THandleFilterDictionaryEventDataElemsById,
  THandleGetDictionaryEvents,
  THandleGetDictionaryTransactionTypes,
  THandleGetProductAprsFeesRewards,
  THandleGetProductRule,
  THandleUpdateProductRule,
  TResetProductRule,
} from 'store';
import { ISelectValue } from 'types';

interface IRulesForm {
  actionTypes: Array<IActionTypeConfig>;
  actionTypeSelectedValue: ISelectValue;
  aprsOptions: ImmutableArray<ISelectValue>;
  eventDataElemsOptions: ImmutableArray<ISelectValue>;
  eventIdSelectedValue: ISelectValue;
  events: Array<IDictionaryEvent>;
  feesOptions: ImmutableArray<ISelectValue>;
  filterDictionaryEventDataElemsById: THandleFilterDictionaryEventDataElemsById;
  getEvents: THandleGetDictionaryEvents;
  getProductAprsFeesRewards: THandleGetProductAprsFeesRewards;
  getRule: THandleGetProductRule;
  getTransactionTypes: THandleGetDictionaryTransactionTypes;
  isEventsLoading: boolean;
  isReadOnly: boolean;
  isRuleLoading: boolean;
  onCancel?: () => void;
  productType: string | number;
  resetRule: TResetProductRule;
  rewardsOptions: ImmutableArray<ISelectValue>;
  transactionTypesOptions: ImmutableArray<ISelectValue>;
  updateRule: THandleUpdateProductRule;
}

type TRulesForm = IRulesForm & InjectedFormProps<{}, IRulesForm>;

const ProductRulesForm: React.FC<TRulesForm> = ({
  actionTypes,
  actionTypeSelectedValue,
  aprsOptions,
  change,
  dirty,
  eventDataElemsOptions,
  eventIdSelectedValue,
  events,
  feesOptions,
  filterDictionaryEventDataElemsById,
  getEvents,
  getProductAprsFeesRewards,
  getRule,
  getTransactionTypes,
  handleSubmit,
  isEventsLoading,
  isReadOnly,
  isRuleLoading,
  onCancel,
  productType,
  resetRule,
  rewardsOptions,
  transactionTypesOptions,
  updateRule,
  valid,
}) => {
  React.useEffect(
    () => {
      Promise.all([
        getEvents(),
        getTransactionTypes(),
        getProductAprsFeesRewards(),
      ]);
    },
    [getEvents, getTransactionTypes, getProductAprsFeesRewards]
  );

  React.useEffect(
    () => {
      if (eventIdSelectedValue) {
        filterDictionaryEventDataElemsById({ eventId: eventIdSelectedValue });
      }
    },
    [filterDictionaryEventDataElemsById, eventIdSelectedValue]
  );

  React.useEffect(
    () => {
      return () => resetRule();
    },
    [resetRule]
  );

  const selectedEventId = React.useMemo(
    () => eventIdSelectedValue && eventIdSelectedValue.value,
    [eventIdSelectedValue]
  );

  const selectedActionType = React.useMemo(
    () => actionTypeSelectedValue && actionTypeSelectedValue.value,
    [actionTypeSelectedValue]
  );

  const eventsOptions = React.useMemo(
    () => events
      .filter(el => el.productType === productType)
      .map(el => {
        return { value: el.value, label: el.label };
      }),
    [productType, events]
  );

  const actionTypesOptions = React.useMemo(
    () => actionTypes
      .filter(el => el.productType === productType)
      .filter(el => el.eventIds.find(id => id === selectedEventId))
      .map(el => {
        return { value: el.value, label: el.label };
      }),
    [productType, selectedEventId, actionTypes]
  );

  const handleGetRule = React.useCallback(
    (fieldName, value) => {
      if (value) {
        change('script', null);

        if (fieldName === 'eventId') {
          change('actionType', null);
        } else {
          change(fieldName, value);
          getRule();
        }
      }
    },
    [change, getRule]
  );

  const handleGetRuleByChange = React.useCallback(
    (value: ISelectValue, val: ISelectValue, prevVal: ISelectValue, fieldName: string) => {
      handleGetRule(fieldName, value);
    },
    [handleGetRule]
  );

  // eslint-disable-next-line
  const handleSubmitForm = React.useCallback(
    handleSubmit(updateRule),
    [handleSubmit, updateRule]
  );

  return (
    <form onSubmit={isReadOnly ? null : handleSubmitForm}>
      <ProductRules
        actionTypesOptions={actionTypesOptions}
        actionTypeValue={selectedActionType}
        aprsOptions={aprsOptions}
        changeFormField={change}
        eventDataElemsOptions={eventDataElemsOptions}
        eventsOptions={eventsOptions}
        eventValue={selectedEventId}
        feesOptions={feesOptions}
        handleGetRule={handleGetRule}
        isEventsLoading={isEventsLoading}
        isReadOnly={isReadOnly || isRuleLoading}
        isRuleLoading={isRuleLoading}
        onChangeValues={handleGetRuleByChange as any}
        rewardsOptions={rewardsOptions}
        transactionTypesOptions={transactionTypesOptions}
      />
      <OkCancelButtons
        okText="Save"
        cancelText="Close"
        onCancel={onCancel}
        disabledOk={!valid}
        withCancelConfirmation={dirty}
        hideOk={isReadOnly}
      />
    </form>
  );
};

export default reduxForm<{}, IRulesForm>({
  form: formNamesConst.PRODUCT_RULES,
  destroyOnUnmount: true,
  enableReinitialize: true,
})(ProductRulesForm);
