import React, { ChangeEvent } from 'react';
import { EventWithDataHandler, Field } from 'redux-form';
import { ImmutableArray } from 'seamless-immutable';

import { Box, Flex } from '@rebass/grid';
import styled from 'theme';

import { actionTypesConst, productRuleScriptSnippets } from 'consts';

import {
  Button,
  Dropdown,
  DropdownOption,
  HighlightCodeField,
  InfoCircleIcon,
  SelectField,
  SmallText,
  Spinner,
} from 'components';
import { ISelectValue } from 'types';
import { formErrorUtil } from 'utils';

const ScriptWrapper = styled(Box)`
  position: relative;
  margin-top: 10px;

  .loading {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 10;
  }

  .script-warning {
    position: absolute;
    top: 0;
    left: 55px;
    padding-right: 10px;

    * {
      color: ${({ theme }) => theme.colors.normalAccent};
    }
  }

  .icon {
    margin-right: 3px;
  }
`;

const AvailableItemsWrapper = styled(Box)`
  .item {
    font-size: 13px;
    border-bottom: 1px solid ${({ theme }) => theme.colors.lighterGray};
    white-space: pre-line;
    transition: all .1s linear;

    &.is-code {
      font-family: ${({ theme }) => theme.fonts.code};
      font-size: 10px;
    }

    &:hover {
      color: ${({ theme }) => theme.colors.normalAccent};
    }
  }

  &.is-disabled {
    opacity: .5;
    pointer-events: none;
  }
`;

const HiddenBox = styled(Box)`
  display: none;
`;

interface IProductRules {
  actionTypesOptions: Array<ISelectValue>;
  actionTypeValue: string | number;
  aprsOptions: ImmutableArray<ISelectValue>;
  changeFormField: (field: string, value: string | ISelectValue) => void;
  eventDataElemsOptions: ImmutableArray<ISelectValue>;
  eventsOptions: Array<ISelectValue>;
  eventValue: number | string;
  feesOptions: ImmutableArray<ISelectValue>;
  handleGetRule: (field: string, value: string | ISelectValue) => void;
  isEventsLoading: boolean;
  isReadOnly: boolean;
  isRuleLoading: boolean;
  onChangeValues: EventWithDataHandler<ChangeEvent<any>>;
  rewardsOptions: ImmutableArray<ISelectValue>;
  transactionTypesOptions: ImmutableArray<ISelectValue>;
}

const ProductRules: React.FC<IProductRules> = ({
  actionTypesOptions,
  actionTypeValue,
  aprsOptions,
  changeFormField,
  eventDataElemsOptions,
  eventsOptions,
  eventValue,
  feesOptions,
  handleGetRule,
  isEventsLoading,
  isReadOnly,
  isRuleLoading,
  onChangeValues,
  rewardsOptions,
  transactionTypesOptions,
}) => {
  const isSelectedRule = React.useMemo(
    () => {
      return eventValue && actionTypeValue;
    },
    [eventValue, actionTypeValue]
  );

  const scriptWarning = React.useMemo(
    () => {
      let warning = '';

      if (eventValue && actionTypeValue) {
        warning = 'The rule with empty script will be deleted';
      } else if (!eventValue) {
        warning = 'Select an event to enable the editor';
      } else if (!actionTypeValue) {
        warning = 'Select an action type to enable the editor';
      }

      return warning;
    },
    [eventValue, actionTypeValue]
  );

  const actionTypesWarning = React.useMemo(
    () => {
      let warning = '';

      if (!eventValue) {
        warning = 'Select an event for displaying available action types';
      } else if (!actionTypesOptions.length) {
        warning = 'No available action types for selected event';
      }

      return warning;
    },
    [eventValue, actionTypesOptions]
  );

  const availableItemsToInsert = React.useMemo(
    () => {
      return [
        {
          options: eventDataElemsOptions,
          name: 'Event data elements',
          noDataMessage: 'No available data elements',
          isVisible: true,
        },
        {
          options: transactionTypesOptions,
          name: 'Transaction types',
          noDataMessage: 'No available transaction types',
          isVisible: true,
        },
        {
          options: aprsOptions,
          name: 'APRs',
          noDataMessage: 'No available APRs',
          isVisible: actionTypeValue === actionTypesConst.SET_APR,
        },
        {
          options: feesOptions,
          name: 'Fees',
          noDataMessage: 'No available fees',
          isVisible: actionTypeValue === actionTypesConst.ADD_FEE,
        },
        {
          options: rewardsOptions,
          name: 'Rewards',
          noDataMessage: 'No available rewards',
          isVisible: actionTypeValue === actionTypesConst.ADD_REWARD,
        },
        {
          options: productRuleScriptSnippets,
          name: 'Code snippets',
          noDataMessage: 'No available code snippets',
          isVisible: true,
        },
      ];
    },
    [
      eventDataElemsOptions,
      transactionTypesOptions,
      aprsOptions,
      feesOptions,
      rewardsOptions,
      actionTypeValue,
    ]
  );

  const insertItem = React.useCallback(
    (value: number | string, shiftCharCount?: number) => {
      const scriptEditor = document.querySelector('#rule-script') as HTMLInputElement;
      const currentText = scriptEditor.value;
      const startIndex = scriptEditor.selectionStart;

      const startPart = currentText.slice(0, startIndex);
      const endPart = currentText.slice(startIndex);
      const resultText = `${startPart}${value}${endPart}`;

      const cursorShiftChars = shiftCharCount
      ? startPart.length + shiftCharCount
      : `${startPart}${value}`.length;

      changeFormField('script', resultText);
      scriptEditor.focus();

      setTimeout(() => scriptEditor.setSelectionRange(cursorShiftChars, cursorShiftChars), 50);
    },
    [changeFormField]
  );

  return (
    <Box mx="-8px">
      <Flex
        alignItems="flex-start"
        flexWrap="wrap"
      >
        <Box width="270px" p="8px">
          <Field
            id="eventId"
            name="eventId"
            component={SelectField}
            label="Event"
            placeholder="Select Event"
            options={eventsOptions}
            isLoading={isEventsLoading}
            isClearable={false}
            onChange={onChangeValues}
            validate={[formErrorUtil.isRequired]}
          />
        </Box>
        <HiddenBox width={[1 / 3]} p="8px">
          <Field
            id="actionType"
            name="actionType"
            component={SelectField}
            label="Action Type"
            placeholder="Select Action Type"
            options={actionTypesOptions}
            isClearable={false}
            onChange={onChangeValues}
            validate={[formErrorUtil.isRequired]}
          />
        </HiddenBox>
        <Box width={[2 / 3]} p="8px">
          <Box mb="5px" pl="9px">
            <SmallText>Action Types</SmallText>
          </Box>
          {actionTypesOptions.length
            ? (
              <Flex alignItems="center" flexWrap="wrap">
                {actionTypesOptions.map(type => (
                  <Button
                    key={type.value}
                    text={type.label}
                    size="11"
                    isFocused={actionTypeValue === type.value}
                    classNames={['is-tabs']}
                    type="reset"
                    onClick={() => handleGetRule('actionType', type)}
                  />
                ))}
              </Flex>
            )
            : (
              <Box mb="5px" pl="9px">
                <SmallText>{actionTypesWarning}</SmallText>
              </Box>
            )
          }
        </Box>
        <Flex width={[1]}>
          <ScriptWrapper width={[7 / 9]}>
            {isRuleLoading && (
              <div className="loading">
                <Spinner isFixed={false} />
              </div>
            )}
            <Flex
              alignItems="center"
              className="script-warning"
            >
              <InfoCircleIcon className="icon" size="16" />
              <SmallText>{scriptWarning}</SmallText>
            </Flex>
            <Box width={[1]} p="0 10px 10px">
              <Field
                id="rule-script"
                name="script"
                component={HighlightCodeField}
                placeholder={isSelectedRule ? 'Write your rule using JavaScript code here...' : ''}
                label="Script"
                fontSize={11}
                focused={true}
                height="calc(100vh - 270px)"
                disabled={!isSelectedRule || isReadOnly}
              />
            </Box>
          </ScriptWrapper>
          <AvailableItemsWrapper
            width={[2 / 9]}
            p="40px 15px"
            className={isSelectedRule ? '' : 'is-disabled'}
          >
            <Box mb="5px">
              <SmallText>Parameters to insert</SmallText>
            </Box>
            {availableItemsToInsert.map((item, index) => {
              return item.isVisible && (
                <Box width={[1]} py="7px" key={index}>
                  <Dropdown
                    selectable={false}
                    ToggleButtonComponent={(
                      <SmallText fontSize="14px" upperCase={true}>
                        {item.name}
                      </SmallText>
                    )}
                  >
                    {item.options.length
                      ? item.options.map((el: {
                        value: string | number;
                        label: string;
                        shiftCharCount?: number;
                      }) => (
                        <DropdownOption key={el.value}>
                          <div
                            className={`item ${item.name === 'Code snippets' ? 'is-code' : ''}`}
                            title="Select for inserting"
                            onClick={() => insertItem(el.value, el.shiftCharCount)}
                          >
                            {el.label}
                          </div>
                        </DropdownOption>
                      ))
                      : (
                        <DropdownOption>
                          <SmallText>{item.noDataMessage}</SmallText>
                        </DropdownOption>
                      )}
                  </Dropdown>
                </Box>
              );
            })}
          </AvailableItemsWrapper>
        </Flex>
      </Flex>
    </Box >
  );
};

export default ProductRules;
